import ReactDOM from 'react-dom';
import { useState } from 'react';

const TextOutput = ({ name }: { name: string }) => {
  return (
    <p>Hello, {name}!</p>
  );
}

const TextInput = () => {
  const [str, setStr] = useState('初期値');

  return (
    <div>
    <form >
      <label>
        入力欄<br />
        <textarea
          id="inputForm"
          value={str}
          onChange={event => setStr(event.target.value)}
        />
      </label>
    </form>
    <TextOutput name={str} />
    </div>
  );
}

ReactDOM.render(
  <TextInput />,
  document.getElementById('root')
);